a, body {
    color: #000
}

*, ::after, ::before {
    padding: 0;
    margin: 0;
    -webkit-tap-highlight-color: transparent;
    -webkit-box-sizing: border-box;
    /*  for iOS */
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  outline: none;
}
*:not(input,textarea) {
  -webkit-touch-callout: none;
  /*-webkit-user-select: none;*/
  -webkit-user-select: auto;
}
body {
    font-size: 14px;
    font-family: "Microsoft YaHei" sans-serif;
    -webkit-text-size-adjust:none;  /*  */
    -webkit-appearance: none;       /**/
    -webkit-user-drag: none;        /*-webkit-user-drag CSS */
    background: #EDEEF0;
    -webkit-overflow-scrolling: touch;
    width: 100%;
    height: 100%;

}

a {
    text-decoration: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0); /*Android  a  transparent */
}

ul {
    list-style: none
}

input, textarea {
    border: none;
    resize: none;
    outline: 0;
    -webkit-appearance: none
}

.clearfix::after, .clearfix::before {
    content: '.';
    height: 0;
    line-height: 0;
    clear: both;
    display: block;
    visibility: hidden
}
i{
  font-style: normal;
}
select,option{
  -ms-progress-appearance:none;
  -moz-appearance:none;
  -webkit-appearance:none;
}




